<template>
  <div class="msgAll">
    <div class="header">
      <p></p>
    </div>
    <div class="main">
      <div class="tabs">
        <el-tabs
          v-model="status"
          type="border-card"
          class="el-tab"
          :tab-position="tabPosition"
          @tab-click="handleClick()"
        >
          <el-tab-pane name="0" label="">
            <div v-loading="loading">
              <!--  -->
              <div v-show="messages.length == 0" class="no-message">
                <p></p>
              </div>
              <!--  -->
              <div class="message">
                <a class="msgline clickable">
                  <MessageLine
                    v-for="(item, key, index) in messages"
                    :key="index"
                    :row="item"
                    @showrow="showDetails()"
                  />
                </a>
              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane name="1" label="">
            <div v-loading="loading">
              <!--  -->
              <div v-show="messages.length == 0" class="no-message">
                <p></p>
              </div>
              <!--  -->
              <div class="message">
                <a class="msgline clickable">
                  <MessageLine
                    v-for="item in messages"
                    :key="item.index"
                    class="message-line"
                    :row="item"
                    @showrow="showDetails()"
                  />
                </a>
              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane name="all" label="">
            <div v-loading="loading">
              <!--  -->
              <div v-show="messages.length == 0" class="no-message">
                <p></p>
              </div>
              <!--  -->
              <div class="message">
                <a class="msgline clickable">
                  <MessageLine
                    v-for="(item, key, index) in messages"
                    :key="index"
                    :row="item"
                    @showrow="showDetails()"
                  />
                </a>
              </div>
            </div>
          </el-tab-pane>
        </el-tabs>
        <el-button
          v-if="(this.status == 'all'|| this.status == 0)"
          class="all-button"
          type="primary"
          @click="ReadAllMessage()"
        >
          
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import MessageLine from './MessageLine.vue'
import { getCurrentAllmessage, getAllRead } from '@/api/message.js'

export default {
  name: 'MessageSquare',
  components: {
    MessageLine
  },
  data() {
    return {
      loading: false,
      status: '0',
      tabPosition: 'top',
      messages: [],
      row: {},
      total: null
    }
  },
  mounted() {
    this.handleGet(this.status)
  },
  methods: {
    async handleGet(status) {
      this.loading = true
      try {
        const data = await getCurrentAllmessage(status)
        // 
        this.total = data.total
        // 
        const message_data = data.data
        this.row = message_data
        this.messages = message_data
        // 
        for (let i = 0; i < this.total; i++) {
          if (message_data[i].content.indexOf('') != -1) {
            this.row[i].title = ''
          }
          if (message_data[i].content.indexOf('') != -1) {
            this.row[i].title = ''
          }
          if (message_data[i].content.indexOf('') != -1) {
            this.row[i].title = ''
          }
        }
        this.loading = false
      } catch (err) {
        this.messages = []
        this.loading = false
        this.$message({
          type: 'error',
          message: err
        })
      }
    },
    async ReadAllMessage() {
      try {
        const data = await getAllRead()
        this.handleGet(this.status)
      } catch (err) {
        this.$message({
          type: 'error',
          message: err
        })
      }
    },
    // 
    handleClick() {
      this.handleGet(this.status)
    }
  }
}
</script>

