<template>
  <div class="recruit-line">
    <div class="line-head">
      <el-tag :type="type">
        {{ tag }}
      </el-tag>
    </div>
    <div class="line-body" width="100">
      <div class="container">
        <p class="line-title clickable" @click="showDetails(row)">
          {{ row.title }}
        </p>
      </div>
      <div class="blank"></div>
      <div class="container">
        <p class="line-content">
          {{ row.content }}
        </p>
      </div>
    </div>
    <div class="line-end">
      <div class="line-action">
        <el-button size="mini" @click="handleEdit(row)">
          
        </el-button>
        <el-button size="mini" type="danger" @click="changeStatus()">
          {{ buttonText }}
        </el-button>
      </div>
      <p class="line-time">
        {{ row.update_time }}
      </p>
    </div>
    <el-dialog
      v-dialogDrag
      title=""
      :visible.sync="edit.visible"
      :fullscreen="isPhone"
      class="recruit-edit-dialog"
    >
      <div class="recruit-edit-content">
        <el-tag v-if="edit.row.recruit_type == 1">
          
        </el-tag>
        <el-tag v-else type="success">
          
        </el-tag>
        <el-tag v-if="edit.row.status == 0" type="danger">
          
        </el-tag>
        <el-input v-model="edit.row.title" class="edit-title" placeholder=""></el-input>
        <el-input
          v-model="edit.row.content"
          class="edit-content"
          type="textarea"
          :rows="4"
          :placeholder="edit.row.placeholder"
        ></el-input>
        <p class="warning">
          
        </p>
        <el-button class="edit-button" type="primary" @click="_handleEdit()">
          
        </el-button>
        <el-button class="edit-button" @click="edit.visible = false">
          
        </el-button>
      </div>
    </el-dialog>
    <el-dialog
      v-dialogDrag
      title=""
      :visible.sync="details.visible"
      :fullscreen="isPhone"
      class="recruit-details-dialog"
    >
      <div class="recruit-details-content">
        <el-tag v-if="details.row.type == 1">
          
        </el-tag>
        <el-tag v-else type="success">
          
        </el-tag>
        <el-tag v-if="details.row.status == 0" type="danger">
          
        </el-tag>
        <p class="details-title">
          <i class="el-icon-document icon"></i>{{ details.row.title }}
        </p>
        <p class="details-content">
          <i class="el-icon-menu icon"></i>{{ details.row.content }}
        </p>
        <p class="details-author">
          <i class="el-icon-author icon"></i>{{ user_name }}
        </p>
        <p class="details-time">
          {{ details.row.create_time }}
        </p>
        <p class="details-time">
          {{ details.row.update_time }}
        </p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios'
import { mapGetters } from 'vuex'
import { showAsynConfirm } from '@/mixins/messageBox'
import { getByUserId } from '@/api/common'

export default {
  name: 'NoteLine',
  props: {
    row: Object
  },
  data() {
    return {
      details: {
        row: {},
        visible: false
      },
      edit: {
        row: {},
        visible: false
      },
      user_name: ''
    }
  },
  mounted() {
    this.getUserName()
  },
  methods: {
    showDetails(data) {
      (this.details.visible = true),
      (this.details.row = Object.assign({}, data))
    },
    // (copy)
    handleEdit(data) {
      const map = {
        1: '',
        0: ''
      }
      this.edit.row = Object.assign({}, data)
      this.edit.row.placeholder = map[data.recruit_type]
      this.edit.visible = true
    },
    _handleEdit() {
      showAsynConfirm('', '', done => {
        this._submitEdit(done)
      })
    },
    async getUserName() {
      try {
        const data = await getByUserId(this.row.user_id)
        this.user_name = data.user_name
      } catch (e) {
        this.$message({
          type: 'error',
          message: ',' + e
        })
      }
    },
    _submitEdit(done) {
      const fields = ['status', 'title', 'content']
      const data = {}
      for (const i in fields) {
        const k = fields[i]
        data[k] = this.edit.row[k]
      }
      axios
        .put('/api/recruit/' + this.row.recruit_id.toString(), data)
        .then(response => {
          const data = response.data
          done()
          this.$emit('refresh')
          if (data.success == 1) {
            this.$message({
              type: 'success',
              message: ''
            })
            this.edit.visible = false
          } else {
            done()
            this.$message({
              type: 'warning',
              message: data.err_msg
            })
          }
        })
        .catch(err => {
          done()
          this.$message({
            type: 'error',
            message: err
          })
          this.errorOpen()
        })
    },
    errorOpen() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        callback: action => {
          this.$message({
            type: 'info',
            message: `action: ${action}`
          })
          this.edit.visible = false
        }
      })
    },
    changeStatus() {
      showAsynConfirm(this.changeMessage, '', done => {
        this._changeStatus(done)
      })
    },
    _changeStatus(done) {
      // 
      this.row.status = this.row.status == 1 ? 0 : 1
      const fields = ['status', 'title', 'content']
      const data = {}
      for (const i in fields) {
        const k = fields[i]
        data[k] = this.row[k]
      }
      axios
        .put('/api/recruit/' + this.row.recruit_id.toString(), data)
        .then(response => {
          done()
          const data = response.data
          this.$emit('refresh')
          this.$message({
            type: 'success',
            message: this.successMessage
          })
        })
        .catch(err => {
          done()
          this.$message({
            type: 'error',
            message: err
          })
        })
    }
  },
  computed: {
    ...mapGetters({
      isPhone: 'isPhone'
    }),
    type() {
      if (this.row.status == 0) {
        return 'danger'
      } else {
        if (this.row.recruit_type == 1) {
          return ''
        } else {
          return 'success'
        }
      }
    },
    tag() {
      if (this.row.status == 0) {
        return ''
      } else {
        if (this.row.recruit_type == 1) {
          return ''
        } else {
          return ''
        }
      }
    },
    buttonText() {
      if (this.row.status == 0) {
        return ''
      } else {
        return ''
      }
    },
    changeMessage() {
      if (this.row.status == 0) {
        return ''
      } else {
        return ''
      }
    },
    successMessage() {
      if (this.row.status == 0) {
        return ''
      } else {
        return ''
      }
    }
  }
}
</script>

