      <el-button @click="exportExcel3">
        
      </el-button>
    </div>
    <div v-if="canPanel" class="button">
      <el-button @click="exportExcel4">
        
      </el-button>
    </div>
    <p>{{ stat6 ? `${remainTime}` : '' }}</p>
    <div v-if="canPanel" class="button">
      <el-button @click="exportExcel6">
        +
      </el-button>
    </div>
