import Vue from 'vue'

/**
 * 
 * @param {string} tips 
 * @param {string} cancelMsg 
 * @param {function} confirmCallback 
 */
export function showConfirm(tips, cancelMsg, confirmCallback) {
  Vue.prototype.$confirm(tips, '', {
    confirmButtonText: '',
    cancelButtonText: '',
    type: 'warning'
  }).then(() => {
    confirmCallback()
  }).catch(() => {
    Vue.prototype.$message({
      type: 'info',
      message: cancelMsg
    })
  })
}

/**
 * 
 * 
 * showAsynConfirm('', '', (close) => {
 *   this.get('xxx').then((data) => {
 *     this.mydata = data;
 *     close(); // closeclose
 *   })
 * })
 * @param {string} tips 
 * @param {string} cancelMsg 
 * @param {function} confirmCallback 
 */
export function showAsynConfirm(tips, cancelMsg, confirmCallback) {
  // 
  // var close;
  Vue.prototype.$confirm(tips, '', {
    confirmButtonText: '',
    cancelButtonText: '',
    type: 'warning',
    beforeClose: (action, instance, done) => {
      if (action == 'confirm') {
        instance.confirmButtonLoading = true
        instance.confirmButtonText = '...'
        confirmCallback(() => {
          instance.confirmButtonLoading = false
          done()
        })
      }
      if (action == 'cancle') {
        instance.confirmButtonLoading = false
      } else {
        done()
        instance.confirmButtonText = ''
      }
      instance.confirmButtonLoading = false
    }
  }).then(() => {
    // beforeClose
  }).catch(() => {
    Vue.prototype.$message({
      type: 'info',
      message: cancelMsg
    })
  })
}
